"use client";

import React from "react";
import { useAuth } from "@/hooks/useAuth";

export default function HomePage() {
  const { user, loading, signIn } = useAuth();

  const handleSignIn = async () => {
    try {
      // For demo purposes, use a fixed test user
      await signIn("testuser@example.com", "password123");
    } catch (error) {
      alert("Sign in failed: " + (error as Error).message);
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (!user) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen space-y-4">
        <h2 className="text-2xl font-semibold">Welcome to iite.bet</h2>
        <button
          onClick={handleSignIn}
          className="px-6 py-3 bg-black text-white rounded hover:bg-gray-800 transition"
        >
          Sign In (Demo)
        </button>
      </div>
    );
  }

  return (
    <div>
      <h2 className="text-xl font-semibold mb-4">Dashboard</h2>
      <p>Welcome back, {user.displayName || user.email}!</p>
      <p>Your role: {user.role}</p>
      {/* Additional dashboard content will go here */}
    </div>
  );
}
